import React from 'react'
import { GifGridItem } from './GifGridItem';
import { useFetchGif } from './hooks/useFetchGif';

export const GitGrid = ({ category }) => {

   
    const { data: images ,loading} = useFetchGif( category );
    console.log(loading)

    return (
        <>
            <h3>{ category }</h3>

            { loading && 'Cargando'}

            <div className="car-grid">
            
                {
                    images.map((img) =>(
                        <GifGridItem 
                            key= { img.id }
                            { ...img }
                        />
                    ))
                }
                
            </div>
        </>
    )
}
